.robot-head {
    transform-origin: 0px 10px;
    animation: searchMotion 3.5s infinite ease-in-out;
}

@keyframes searchMotion {
    0% {
        transform: rotate(0deg);
    }
    10% {
        transform: rotate(-10deg);
    }
    30% {
        transform: rotate(0deg);
    }
    50% {
        transform: rotate(10deg);
    }
    70% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(0deg);
    }
}
