@tailwind base;
@tailwind components;
@tailwind utilities;

body {
    font-family: 'Roboto', sans-serif;
}

body {
    @apply bg-[#f0f3f5] dark:bg-[#181818];
}

.f-v {
    @apply flex flex-col;
}

.f-h {
    @apply flex flex-row;
}

.form-label {
    @apply text-sm text-[#6A6A6A] dark:text-[#8A8A8A];
}

.form-input {
    @apply px-2 py-1 h-[28px] rounded-md text-sm text-[#181818] dark:text-[#FFFFFF] bg-[#FFFFFF] dark:bg-[#181818] focus:outline-none focus:ring-1 focus:ring-[#181818] dark:focus:ring-[#FFFFFF];
}

.form-input:disabled {
    @apply px-2 py-1 h-[28px] border text-[#181818] dark:text-[#FFFFFF] border-[#b8b8b8] dark:border-[#2A282B] bg-[#d4d6d8] dark:bg-[#1f1e1f];
}

.form-select {
    @apply px-2 py-1 h-[28px] rounded-md text-sm text-[#181818] dark:text-[#FFFFFF] bg-[#FFFFFF] dark:bg-[#181818] focus:outline-none focus:ring-1 focus:ring-[#181818] dark:focus:ring-[#FFFFFF];
}

.form-select:disabled {
    @apply px-2 py-1 h-[28px] border text-[#181818] dark:text-[#FFFFFF] border-[#b8b8b8] dark:border-[#2A282B] bg-[#d4d6d8] dark:bg-[#1f1e1f];
}

.form-textarea {
    @apply p-2 rounded-md text-sm bg-[#FFFFFF] dark:bg-[#181818] text-[#181818] dark:text-[#FFFFFF] focus:outline-none focus:ring-1 focus:ring-[#181818] dark:focus:ring-[#FFFFFF];
}

.form-textarea:disabled {
    @apply p-2 rounded-md text-sm bg-[#d4d6d8] dark:bg-[#1F1E1F] text-[#181818] dark:text-[#FFFFFF] border border-[#b8b8b8] dark:border-[#2A282B];
}

.form-input-table-row {
    @apply bg-[#FFFFFF] dark:bg-[#181818] text-[#181818] dark:text-[#FFFFFF] focus:outline-none focus:ring-1 focus:ring-[#181818] dark:focus:ring-[#FFFFFF];
}

.form-input-table-row:disabled {
    @apply border text-[#181818] dark:text-[#FFFFFF] border-[#b8b8b8] dark:border-[#2A282B] bg-[#d4d6d8] dark:bg-[#1f1e1f];
}

.form-textarea-table-row {
    @apply bg-[#FFFFFF] dark:bg-[#181818] text-[#181818] dark:text-[#FFFFFF] focus:outline-none focus:ring-1 focus:ring-[#181818] dark:focus:ring-[#FFFFFF];
}

.form-textarea-table-row:disabled {
    @apply text-[#181818] dark:text-[#FFFFFF] border-[#b8b8b8] dark:border-[#2A282B] bg-[#d4d6d8] dark:bg-[#1f1e1f];
}

.button {
    @apply px-4 py-1 min-w-[120px] rounded-full shadow-md text-xs text-[#181818] dark:text-[#FFFFFF] bg-[#494849] hover:bg-[#656365];

    /* &:disabled {
        @apply text-[#8A8A8A];
    } */
}

/*расчет 3 строк в названии позиции*/
.fourrow {
    margin: 0;
    /* Убираем внешний отступ */
    -webkit-line-clamp: 3;
    /* Число отображаемых строк */
    display: -webkit-box;
    /* Включаем флексбоксы */
    -webkit-box-orient: vertical;
    /* Вертикальная ориентация */
    overflow: hidden;
    /* Обрезаем всё за пределами блока */
}

/*расчет 2 строк в названии позиции*/
.tworow {
    margin: 0;
    /* Убираем внешний отступ */
    -webkit-line-clamp: 2;
    /* Число отображаемых строк */
    display: -webkit-box;
    /* Включаем флексбоксы */
    -webkit-box-orient: vertical;
    /* Вертикальная ориентация */
    overflow: hidden;
    /* Обрезаем всё за пределами блока */
}

/*расчет 1 строк в названии позиции*/
.onerow {
    margin: 0;
    /* Убираем внешний отступ */
    -webkit-line-clamp: 1;
    /* Число отображаемых строк */
    display: -webkit-box;
    /* Включаем флексбоксы */
    -webkit-box-orient: vertical;
    /* Вертикальная ориентация */
    overflow: hidden;
    /* Обрезаем всё за пределами блока */
}

.switch {
    width: 43px;
    height: 25px;
    border-radius: 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 2px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    border: solid 2px #33303300;
}

.switch-on {
    @apply bg-[#83d2b7] dark:bg-[#187a33];
}

.switch-off {
    @apply bg-[#d8dadc] dark:bg-[#656365];
}

.switch-disabled {
    @apply cursor-default bg-[#d4d6d8] dark:bg-[#242225] border border-[#b8b8b8] dark:border-[#333033];
}

.switch-toggle {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: transform 0.3s ease;
}

.toggle-on {
    transform: translateX(13px);
}

.toggle-off {
    transform: translateX(-1px);
}

.toggle-on-disabled {
    @apply bg-[#83d2b7] dark:bg-[#187a33];
}

.toggle-off-disabled {
    @apply bg-[#c8c8c8] dark:bg-[#656365];
}

.custom-scrollbar {
    &::-webkit-scrollbar {
        width: 0.5em;
        height: 0.5em;
        background-color: rgba(180, 180, 180, 0.2);
    }

    &::-webkit-scrollbar-thumb {
        background-color: rgba(100, 100, 100, 0.5);
        border-radius: 3px;

        &:hover {
            background: rgba(200, 200, 200, 0.8);
        }
    }
}

.perspective {
    perspective: 500px;
}

.transform-style-3d {
    transform-style: preserve-3d;
}
