.org-input {
    @apply w-full h-7 px-2 rounded-md border-0 focus:outline-none focus:ring-1 text-sm text-[#181818] dark:text-[#FFFFFF] focus:ring-[#181818] dark:focus:ring-[#FFFFFF] bg-[#f0f3f5] dark:bg-[#181818];
}

.org-input:disabled {
    @apply w-full h-7 px-2 rounded-md border focus:outline-none focus:ring-1 text-sm text-[#181818] dark:text-[#FFFFFF] focus:ring-[#181818] dark:focus:ring-[#FFFFFF] border-[#b8b8b8] dark:border-[#2A282B] bg-[#d4d6d8] dark:bg-[#1f1e1f];
}

.org-input-placeholder::placeholder {
    color: #8a8a8a; /* Цвет placeholder */
    opacity: 1; /* Полностью видимый текст */
}

.org-input-placeholder:focus::placeholder {
    color: #8a8a8a33; /* Цвет placeholder при фокусе */
}

@media print {
    body * {
        visibility: hidden;
    }
    #printable-content,
    #printable-content * {
        visibility: visible;
    }
    #printable-content {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
    }
    .overflow-y-auto {
        overflow: visible !important;
        height: auto !important;
        overflow-y: auto;
    }
    .h-dvh {
        height: auto !important;
    }
}
